@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  font-family: 'Rubik', sans-serif;
  text-align: center;
  background-color: #e8e8e8;
  color: black;
}

.button-light {
  background-color: #e8e8e8;
  border: 2px solid #f05454;
  color: #f05454;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  width: 100px;
  border-radius: 5px;
  padding: 10px;
  margin: 0 10px;
}

.button-dark {
  background-color: #f05454;
  border: 2px solid #f05454;
  color: #e8e8e8;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  width: 100px;
  border-radius: 5px;
  padding: 10px;
  margin: 0 10px;
}

.modal {
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 1;
  top: 15vh;
  width:100vw;
  height: 70vh;
}
  